<template>
  <div class="container-fluid">
    <nav class="navbar navbar-expand fixed-top navbar-light mb-5" style="background-color:#e3f2fd;">
      <span class="mx-auto mt-1" style="font-size:20px;">お気に入り</span>
    </nav>
    <div class="mt-3 row" v-for="(cfav,i) in nekos"  v-bind:key="i">
        <div class="col-2"><img v-bind:src="nekosphots[i]" width="20" ></div>
        <div class="col-7">{{cfav.name}}</div>
        <div class="col-3"><toggle-button :labels="{checked: '買う！', unchecked: 'ある'}" 
                        v-model="toggle"
                        @change="onChangeEventHandler(i)"
                       :color="{checked: '#FF0000', unchecked: '#00FF00', disabled: '#CCCCCC'}"
                        width="60">false</toggle-button></div>
    </div>
    <nav class="navbar fixed-bottom navbar-light mt-5" style="background-color:#e3f2fd;">
      <div class="row">
        <span class="col-4"></span>
        <span class="col-4 text-center"><input type="image" v-bind:src="plusbutton" width="30" height="30" v-on:click="goPageAdd"/></span>
        <span class="col-4"></span>
        <span class="w-100"></span>
        <span class="text-center col-4"><button class="btn-social-square btn-social-square--feedly" v-on:click="goPageFavorite">お気に入り</button></span>
        <span class="text-center col-4"><button class="btn-social-square btn-social-square--feedly" v-on:click="goPageCheckList">買い物リスト</button></span>
        <span class="text-center col-4"><button class="btn-social-square btn-social-square--feedly" v-on:click="goPageHistory">買い物履歴</button></span>
      </div>
    </nav>
  </div>
</template>
<script>
    export default{
        data(){
            return{nekos:[],
                   plusbutton: require('@/assets/app_button_plus.png')
            }            
//            return{nekos:[],
//                imgPath: require('@/assets/pizza.png')
//            }
        },
        created(){
//            alert ("created")

            this.imgPath=require('@/assets/pizza.png')
            this.todos=JSON.parse(window.localStorage.getItem('todos'))||[];
//            this.nekos = JSON.parse(window.localStorage.getItem('favs'))||[];
            
            if (this.nekos.length == 0) {
                this.nekos = [{id:"1",name:"💛ホルスタイン牛乳",status:false,fname:"milk.png"},
                            {id:"2",name:"💛愛知県産トマト",status:false,fname:"tomato.png"},
                            {id:"3",name:"💛山梨産デラウェア",status:false,fname:"grape.png"}];
            }else{
                // すでにあるので何もしない
            }
            
            // 写真データ用の配列を用意する
            this.nekosphots = [];
            
            //配列分画像を作る
            for (let k = 0; k < this.nekos.length; k++) {
                this.nekosphots[k] = require("@/assets/" + this.nekos[k].fname );
            }
            
            //this.Constfavs=JSON.parse(window.localStorage.getItem('favs'))||[];
        },
        methods:{
            goPageFavorite(){
                this.$router.push('/todos/favorite');
            },
            goPageCheckList(){
                this.$router.push('/');
            },
            goPageHistory(){
                this.$router.push('/todos/history');
            },
            // トグルボタン変更イベント
            onChangeEventHandler: function(i){
                // 買い物チェックリストの中身
                const todos=JSON.parse(window.localStorage.getItem('todos'))||[];
//                const Constfavs=JSON.parse(window.localStorage.getItem('favs'))||[];
                const selectSyouhin = this.nekos[i].name
                
                
                // トグル＝true（買う）の場合
                if (this.toggle) {
                    // 買い物チェックリスト（todos）の中にすでにお気に入りがいた場合
                    // todosの中からお気に入り商品を消さない
                    
                    let count = 0;
                    for (let j = 0; j < todos.length; j++) {
                        if (todos[j] === selectSyouhin) {
                            count = 1;
                        }
                    }
                    // todos内に存在しない場合
                    if (count == 0) {
                    
                        // 買うものリストに追加
                        todos.push(selectSyouhin);
                        //alert('todos=[' + todos + ']');
                        window.localStorage.setItem('todos',JSON.stringify(todos));
                    }
                    
                    
                // トグル＝false（ある）の場合
                }else{
                    // 買い物チェックリスト（todos）の中にすでにお気に入りがいた場合
                    // todosの中からお気に入り商品を消す
                    for (let j = 0; j < todos.length; j++) {
                        if (todos[j] === selectSyouhin) {
                            todos.splice(j, 1)
                            window.localStorage.setItem('todos',JSON.stringify(todos));
                        }
                        
                    }
                }
                // constfavsに変更したステータスを設定
                this.nekos[i].status = this.toggle
                // ステータスの状態を保存する
                window.localStorage.setItem('favs',JSON.stringify(this.nekos));
            },
            getimage(){
                return require('@/assets/pizza.png');
            }
        }
    }
</script>